* {
  margin: 0;
  padding: 0;
  box-sizing: content-box;
}

body {
  max-width: 1024px;
  margin: 0 auto;
}
body #header {
  max-width: 1024px;
  display: flex;
  justify-content: flex-end;
  background-color: #ccc;
  margin: 0 auto;
  padding: 25px 0;
}
body #header .login_link {
  text-transform: uppercase;
  text-decoration: none;
  color: #363135;
  font-size: 0.8rem;
  font-weight: 700;
}
body form {
  display: flex;
  flex-direction: column;
  width: 25%;
  gap: 5px;
  margin: 15px 0 0;
}
body form li {
  list-style: none;
  display: flex;
  justify-content: space-between;
}
body form #form_submit_btn {
  padding: 12px 8px;
  background-color: #ced;
  border-radius: 5px;
  border: 2px solid #ccc;
  outline: #0063aa;
  margin-top: 15px;
  cursor: pointer;
  font-size: 1.2rem;
  font-weight: 700;
  letter-spacing: 1.2px;
  text-transform: uppercase;
  color: #0063aa;
}